var Messages = require('./messages');
import {WeaLocaleProvider} from 'ecCom';
const getLabel = WeaLocaleProvider.getLabel;

require('./lang/en');

var require_method = require;

var container = {

  messages: {},

  /**
   * Set messages for language
   *
   * @param {string} lang
   * @param {object} rawMessages
   * @return {void}
   */
  _set: function(lang, rawMessages) {
    this.messages[lang] = rawMessages;
  },

  /**
   * Set message for given language's rule.
   *
   * @param {string} lang
   * @param {string} attribute
   * @param {string|object} message
   * @return {void}
   */
  _setRuleMessage: function(lang, attribute, message) {
    this._load(lang);
    if (message === undefined) {
      message = this.messages[lang].def;
    }

    this.messages[lang][attribute] = message;
  },

  /**
   * Load messages (if not already loaded)
   *
   * @param  {string} lang
   * @return {void}
   */
  _load: function(lang) {
    // if (!this.messages[lang]) {
      try {
        var rawMessages = {
          accepted: getLabel('386937', ':attribute'),
          alpha: getLabel('386936', ':attribute'),
          alpha_dash: getLabel('386939', ':attribute,'),
          alpha_num: getLabel('386940', ':attribute'),
          between: getLabel('386941', ':attribute(,):min:max'),
          confirmed: getLabel('386942', ':attribute'),
          email: getLabel('386943', ':attribute'),
          date: getLabel('386944', ':attribute'),
          def: getLabel('386945', ':attribute'),
          digits: getLabel('386946', ':attribute:digits'),
          different: getLabel('386947', ':attribute:different'),
          'in': getLabel('386948', ':attribute'),
          integer: getLabel('386949', ':attribute'),
          min:{
            numeric: getLabel('386950', ':attribute:min'),
            string: getLabel('386951', ':attribute:min')
          },
          max: {
            numeric: getLabel('386952', ':attribute:max'),
            string: getLabel('386953', ':attribute:max')
          },
          not_in: getLabel('386954', ':attribute'),
          numeric: getLabel('386955', ':attribute'),
          present: getLabel('386956', ':attribute'),
          required: getLabel('386957', ':attribute '),
          required_if: getLabel('386958', ':other:value,:attribute'),
          same: getLabel('386959', ':attribute:same'),
          size: {
            numeric: getLabel('386960', ':attribute:size'),
            string: getLabel('386962', ':attribute:size')
          },
          string: getLabel('386963', ':attribute'),
          url: getLabel('386943', ':attribute'),
          regex: getLabel('386943', ':attribute'),
          attributes: {}
        }
        this._set(lang, rawMessages);
      } catch (e) {}
    // }
  },

  /**
   * Get raw messages for language
   *
   * @param  {string} lang
   * @return {object}
   */
  _get: function(lang) {
    this._load(lang);
    return this.messages[lang];
  },

  /**
   * Make messages for given language
   *
   * @param  {string} lang
   * @return {Messages}
   */
  _make: function(lang) {
    this._load(lang);
    return new Messages(lang, this.messages[lang]);
  }

};

module.exports = container;



